import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import '../static/css/app.css'
import App from './App.vue'
import VueRouter from 'vue-router'
import 'font-awesome/css/font-awesome.min.css'
// import Mock from './mock'
// Mock.bootstrap()
import { getMenu } from './api/api'

getMenu().then((res) => {
  let userName = res.data.result.user_name
  let menus = res.data.result.menus
  let pageItems = res.data.result.page_items
  localStorage.setItem('menus', menus)
  localStorage.setItem('page_items', pageItems)
  localStorage.setItem('userName', userName)
  // 为什么不用静态引用，因为routes里权限控制是从localstorage里取数据渲染的，所以要写在回调里
  import('./routes').then(routes => {
    const routeArray = routes.default
    Vue.use(ElementUI)
    Vue.use(VueRouter)
    const router = new VueRouter({
      routes: routeArray
    })
    new Vue({
      router,
      el: '#app',
      render: h => h(App)
    })
  })
})



